*
{
    padding: 0;
    margin: 0;
    
}
.inputForm
{
    background-color:rgb(247, 240, 163) ;
    box-shadow: -1px 6px 46px 0px rgba(54, 54, 54, 0.75);
    height: 400px;
    width: 380px;
    margin-left: 450px;
    margin-top: 50px;
    padding: 20px;
    border-radius: 10px;
    
}
.inputForm input, textarea
{
    width: 300px;
    border: 1px solid white;
    padding: 10px 10px;
    outline: none;
    margin-bottom: 20px;
    font-size: 16px;
}
.inputForm button
{
    background-color: #002b5b;
    color: white;
    border-radius: 20px;
    border: none;
    padding: 5px 25px;
    font-size: 14px;
    font-weight: bold;
}